<template>
  <div id="address-cell">

    <div v-if="address">
      <mt-cell 
        :title="address.name + '   电话: ' + address.tel" 
        :label="'地址: ' + address.full_street_address"
        is-link
        href="#!/addresses"
        class="edit"
      >
        <img slot="icon" v-bind:src="locationImgSrc" width="24" height="24" />
      </mt-cell>
    </div>

    <div v-else>
      <mt-cell title="填写送货地址" value="填写" is-link class="addNew" href="#!/addresses">
        <img slot="icon" v-bind:src="locationImgSrc" width="24" height="24" />

      </mt-cell>
    </div>


  </div>
</template>

<script>

export default {
  name: 'AddressCell',

  props: {
    address: Object
  },

  data() {
    return {
      locationImgSrc: require("../../assets/images/location.png")
    }
  }
}
</script>

<style>
#address-cell {
  margin: 0 auto 20px;
}

.blank {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>


