<template>
  

  <div id="add-address">

    <!-- navbar -->
    <checkout-nav-bar :path="'/checkout'" :title="'地址'"></checkout-nav-bar>
    
    
    <div class="address-form">

      <!-- new address -->
      <section class="cell-header"><h6>请填写收货人信息</h6></section>

      <mt-field label="收货人" placeholder="请填入" :value.sync="name"></mt-field>
      <mt-field label="手机" type="tel" placeholder="请填入" :value.sync="tel"></mt-field>
      
      <div class="provCityDist">
        <mt-cell title="省市" is-link :value="provCityDist" @click="show"></mt-cell>
      </div>

      <mt-field label="地址" type="street" placeholder="街、号码、室" :value.sync="street"></mt-field>

      <vue-area :show.sync="showArea" :result.sync="result"></vue-area>
    
      <section style="padding-top: 30px; padding-bottom: 30px;">
        <button 
          class="button success" 
          style="width: 100%;" 
          @click="save" 
          :disabled="isDisabled">保存
        </button>
      </section>

    </div>
    

  </div>

</template>

<script>
import { Field } from 'mint-ui';
import vueArea from 'vue-area'
import REGION_DATA from 'china-area-data'

import CheckoutNavBar from '../checkout/CheckoutNavBar.vue'

export default {
  name: 'AddAddress',

  components: {
    vueArea,
    CheckoutNavBar
  }, 

  data() {
    return {
      name: "",
      tel: "",
      street: "",
      result: {},
      showArea: false
    }
  },

  methods: {
    show() {
      this.$set("showArea", true)
    }, 

    isPresent(field) {
      return (field) && (field.length > 0)
    }, 

    /* check if user logged in */
    isUserLoggedIn() {
      if (typeof APP_SHOP_USER == 'undefined') {
        return false
      }
      
      return (APP_SHOP_USER && (APP_SHOP_USER != "TEMPLATE_APP_SHOP_USER"))
    },

    save() {
      let address = {
        name:     this.name,
        tel:      this.tel,
        province: this.province,
        city:     this.city,
        district: this.district,
        street:   this.street
      }

      // check user logged in
      if (!this.isUserLoggedIn()) {
        return 
      }

      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[AddAddress save callback] response: " + JSON.stringify(response.body))

          // redirect
          let address = response.body
          this.$dispatch('didPickAddress', address)
          window.location.href = "#!/checkout"
      }

      var errorCallback = function(response) {
          console.log("[AddAddress save error] response: " + JSON.stringify(response))

          MessageBox("失败", "抱歉发生了错误，请稍候再试", {
            type: "error"
          })
      }

      // request url 
      var url = API_URL + "/api/addresses"

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET,
          "X-APP-SHOP-USER": APP_SHOP_USER
      }

      // vue-resource
      var params = {"address": address }
      this.$http.post(url, params, {headers: headers}).then(callback, errorCallback)
    }
  },

  computed: {
    province() {
      if (this.result) {
        return this.result.province.name
      } else {
        return ""
      }
    }, 
    
    city() {
      return (this.result) ? this.result.city.name : ""
    }, 

    district() {
      return (this.result) ? this.result.area.name : ""
    }, 

    isDisabled() {
      return (!(
          this.isPresent(this.name) && 
          this.isPresent(this.tel) &&
          this.isPresent(this.street) &&
          this.isPresent(this.province) &&
          this.isPresent(this.city) &&
          this.isPresent(this.district) &&
          this.isPresent(this.street)
        )) 
    },

    provCityDist() {
      if (!((this.result.province) && (this.result.city) && (this.result.area))) {
        return "请输入"
      }

      let prov = this.result.province.name
      let city = this.result.city.name
      let dist = this.result.area.name

      return [prov, city, dist].join('')
    }
  }
}
</script>

<style>

/* checkout content below navbar */ 
#add-address {
  margin-top: 64px;
}

.address-form {
  padding-top: 60px;

  /*gray background simulate tableview*/
  height: 100%;
  background-color: #F6F6F6;
}

/*fix Vue-Address styles*/
.address-form .ProvCityContentList ul {
  padding: 0;
  margin: 0;
}

.address-form .ProvCityContentList li {
  margin: 0;
}

/* fix provCityDist to align same as mint-fields */
.provCityDist .mint-cell-value {
  padding: 0.3em 0.6em;
}
.provCityDist .mint-cell-title {
  width: 105px;
  flex: none;
}

</style>



