<template>
  <div id="checkout-items-wrapper">
    <section class="checkout-items" v-if="checkoutItems && (checkoutItems.length > 0)">
      <checkout-item v-for="checkoutItem in checkoutItems" 
        :item="checkoutItem.item" 
        :qty="checkoutItem.qty" 
        :color="checkoutItem.color" 
        :size="checkoutItem.size"
        :shipping="checkoutItem.shipping">
      </checkout-item>
    </section>

    <section v-else class="center island small mute">
      <p>你的购物车是空的~</p>
    </section>
  </div>

</template>


<script>
import CheckoutItem from './CheckoutItem.vue'

export default {
  name: 'CheckoutItems',

  props: {
    checkoutItems: Array
  },

  components: {
    CheckoutItem
  }
}
</script>

<style>

#checkout-items-wrapper {
  background-color: white; padding-top: 8px; padding-bottom: 8px; 

  margin: 0 auto 20px;

  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.checkout-item + .checkout-item {
  border-top: 1px solid #cccccc;
  padding-top: 8px;
  margin-top: 16px;
}

</style>


