<template>
  <div id="checkout-footer">
    <section class="checkout-footer-container">
      <div>
        <span>总金额</span> 
        <span class="price" style="margin-left: 10px;">
          {{ (totalAmount / 100) | currency "¥ " 2  }}
        </span>
      </div>
      <div>
        <button class="button outline success" style="font-size: 0.85em;" @click="didOrder()" :disabled="disabled">支付</button>
      </div>
    </section>
  </div>
</template>

<script>

export default {
  name: 'CheckoutFooter',

  props: {
    totalAmount: Number,
    disabled: Boolean
  },

  methods: {
    didOrder() {
      this.$dispatch("didOrder")
    }
  }
}
</script>

<style>
#checkout-footer {
  position: fixed;
  bottom: 0;
  z-index: 6; /* underneath cover views */
  width: 100%;
}
.checkout-footer-container {
  display: flex;
  justify-content: space-between;
  align-items: center; 
  border-top: 1px solid #ddd;
  background-color: white;
  padding: 10px 20px;

}

</style>


