<template>
<div id="nav-wrapper">
  <nav class="white three navbar" style="">

    <div class="left-bar-button">
      <a v-link="{path: path, activeClass: 'hide-link'}">
          <img v-bind:src="backImageSrc" style="width: 30px;" />
        </a>
    </div>

    <div class="title">{{ title }}</div>

    <div><!-- right --> </div>
  </nav>
</div>

</template>

<script>

export default {
  name: 'CheckoutNavBar',
  props: {
    path: {
      type: String,
      default: "/cart"
    },
    title: {
      type: String,
      default: "购物车"
    }
  }, 
  data() {
    return {
      backImageSrc: require("../../assets/images/checkout_back_button.png")
    }
  }
}
</script>


<style>
/* native style clear nav */
nav.clear {
  background: none;
  box-shadow: none;
}

nav.navbar {
  height: 64px; padding-top: 20px;
}

nav.three {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left-bar-button {
  padding-left: 20px;
}
.title {
  flex-grow: 1;
  text-align: center;
}

/* old purple branding */
nav.purple {
  background-color: #740092;
}

nav.white {
  background-color: white;
}

</style>


