<template>
	<div class="cartItemQty-form">
		<div class="cartItem-mins-btn" @click="minus" :disabled="cartItem.qty <= 1" > - </div>
		<div class="cartItem-qty-text">{{ cartItem.qty }}</div>
		<div class="cartItem-plus-btn" @click="plus"> + </div>
	</div>
</template>

<script>

export default {
  name: 'CartItemQtyInput',

  props: {
    cartItem: Object
  },

  methods: {
    plus () {
      this.cartItem.qty = this.cartItem.qty + 1
      this.$dispatch('didPlus', this.cartItem, 1)
    },

    minus () {
      if (this.cartItem.qty > 1) {
        this.cartItem.qty = this.cartItem.qty - 1
        this.$dispatch('didMinus', this.cartItem, 1)
      }
    }
  }

}
</script>

<style>
	.cartItemQty-form {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.cartItem-plus-btn {
		width: 30px;
		height: 30px;
		border: 0.5px solid #999;
		border-radius: 5px;
		text-align: center;
		vertical-align: middle;
	}
	
	.cartItem-mins-btn {
		width: 30px;
		height: 30px;
		border: 0.5px solid #999;
		border-radius: 5px;
		text-align: center;
		vertical-align: middle;
	}
	.cartItem-mins-btn[disabled] {
		border: 1px solid #DDD;
    	color:#ACA899;
	}
</style>


