<template>
  <div id="cart" class="cart-view-container">

    <cart-nav-bar></cart-nav-bar>

    <error-view v-show="showError"></error-view>

    <loading-cover v-show="isLoading" transition="fade">
    </loading-cover>

    <div class="body-view">
      <cart-items :cart-items="cartItems"></cart-items>

    <div id="cart-footer">
      <cart-total :total-amount="totalAmount" :disabled="isDisabled"></cart-total>
      <tab-bar-placeholder></tab-bar-placeholder>
    </div>

  </div>
</template>

<script>
import NavBarPlaceholder from './shared/NavBarPlaceholder.vue'
import TabBarPlaceholder from './shared/TabBarPlaceholder.vue'
import LoadingCover from './shared/LoadingCover.vue'
import ErrorView from './shared/ErrorView.vue'

import CartItems from './cart_item/CartItems.vue'
import CartTotal from './cart_item/CartTotal.vue'

import CartNavBar from './cart_item/CartNavBar.vue'

export default {
  name: 'CartView',

  components: {
    CartItems,
    CartTotal,
    NavBarPlaceholder,
    TabBarPlaceholder,
    CartNavBar,
    LoadingCover,
    ErrorView
  },

  data() {
    return {
      cartItems: [],
      totalAmount: 0,
      isLoading: true,
      showError: false
    }
  },

  route: {
    data ({ to }) {

      // refresh
      this.getCart()

      // callback to native
      didMain()
    }
  },

  events: {

    didMinus(cartItem, qty) {
      this.updateTotalAmount()
      this.removeCart(cartItem, qty)
      return true
    },

    didPlus(cartItem, qty) {
      this.updateTotalAmount()
      this.addCart(cartItem, qty)
      return true
    },

    removeItem(cartItem) {

      // remove cartItem 
      for (var i=0; i < this.cartItems.length; i++) {
        if (this.cartItems[i] === cartItem) {
          this.cartItems.splice(i, 1)
          break
        }
      }

      this.updateTotalAmount()
      this.removeCart(cartItem, cartItem.qty) // remove all qty
      return true
    }
  },

  computed: {
    /* check if checkout buttons should be disabled */
    isDisabled() {
      return !(this.cartItems && this.cartItems.length > 0)
    },

    /* check if user logged in */
    isUserLoggedIn() {
      if (typeof APP_SHOP_USER == 'undefined') {
        return false
      }
      
      return (APP_SHOP_USER && (APP_SHOP_USER != "TEMPLATE_APP_SHOP_USER"))
    },

  }, 

  methods: {

    /* update cart total amount */
    updateTotalAmount() {
      let totalAmt = 0;
      for(let ci of this.cartItems) {
        var amt = ci.item.price * ci.qty 
        totalAmt = totalAmt + amt
      }
      
      this.$set("totalAmount", totalAmt)
      return totalAmt
    },

    /* get cart from API */
    getCart () {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[getCart callback] response: " + JSON.stringify(response.body))
          let cart = response.json()
          let cartItems = cart.cart_items
          let totalAmount = cart.total_amount

          vm.$set('cartItems', cartItems)
          vm.$set('totalAmount', totalAmount)

          // remove loading cover
          vm.$set('isLoading', false)
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + JSON.stringify(response));

          // remove loading cover
          vm.$set('isLoading', false)

          // show error
          vm.$set('showError', true)
      }

      // request url 
      var url = API_URL + "/api/cart";

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET, 
          "X-APP-SHOP-USER": APP_SHOP_USER
      };

      // vue-resource
      this.$http.get(url, {headers: headers}).then(callback, errorCallback);
    }, 

    /* Notify API for add qty for item to cart */
    addCart(cartItem, qty) {

      // check user logged in
      if (!this.isUserLoggedIn) {
        return 
      }

      // payload
      var cartItem = {
        item_key : cartItem.item.key,
        size: cartItem.size,
        color: cartItem.color,
        qty: qty
      }

      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[CartView add callback] response: " + JSON.stringify(response.body))
      }

      var errorCallback = function(response) {
          console.log("[CartView add error] response: " + JSON.stringify(response))

          MessageBox("失败", "抱歉发生了错误，请稍候再试", {
            type: "error"
          })
      }

      // request url 
      var url = API_URL + "/api/cart/add"

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET,
          "X-APP-SHOP-USER": APP_SHOP_USER
      }

      // vue-resource
      var params = {"add": cartItem }
      this.$http.patch(url, params, {headers: headers}).then(callback, errorCallback)
    }, 

    /* Notify API for remove qty for item from cart */
    removeCart(cartItem, qty) {

      // check user logged in
      if (!this.isUserLoggedIn) {
        return 
      }

      // payload
      var cartItem = {
        item_key : cartItem.item.key,
        size: cartItem.size,
        color: cartItem.color,
        qty: qty
      }

      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[CartView remove callback] response: " + JSON.stringify(response.body))
      }

      var errorCallback = function(response) {
          console.log("[CartView remove error] response: " + JSON.stringify(response))

          MessageBox("失败", "抱歉发生了错误，请稍候再试", {
            type: "error"
          })
      }

      // request url 
      var url = API_URL + "/api/cart/remove"

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET,
          "X-APP-SHOP-USER": APP_SHOP_USER
      }

      // vue-resource
      var params = {"remove": cartItem }
      this.$http.patch(url, params, {headers: headers}).then(callback, errorCallback)
    },


  }
}
</script>

<style>
/* cart content below navbar */ 
#cart {
  margin-top: 64px;
  padding-top: 20px;
  padding-bottom: 115px;
  position: relative;
}

#cart-footer {
  position: fixed;
  bottom: 0;
  z-index: 6; /* underneath cover views */
  width: 100%;
}
</style>


