<template>
<div id="checkout">

  <!-- navbar -->
  <checkout-nav-bar :path="'/cart'" :title="'结算'"></checkout-nav-bar>
  
  <!-- error cover screen -->
  <error-view v-show="showError"></error-view>

  <!-- loading cover -->
  <loading-cover v-show="isLoading" transition="fade">
  </loading-cover>



  <div class="checkout-body-view">
      <!-- select address -->
    <section class="cell-header"><h6>送货地址</h6></section>
    <address-cell :address="address" ></address-cell>
    
    <!-- cart summary -->
    <section class="cell-header"><h6>购物车</h6></section>
    <checkout-items :checkout-items="checkoutItems"></checkout-items>
    
    <!-- 发票 and other options -->
    <section class="cell-header"><h6>订单选项</h6></section>
    <fapiao-cell></fapiao-cell>

    <!-- loyalty/points select -->
    <section class="cell-header"><h6>积分兑换 -- 即将来临</h6></section>
    <points-select></points-select>

    <!-- subtotals -->
    <section class="cell-header"><h6>订单总共</h6></section>
    <checkout-totals :subtotal-amount="subtotalAmount" :shipping-amount="shippingAmount" :total-amount="totalAmount"></checkout-totals>
    
    <!-- footer -->
    <checkout-footer :total-amount="totalAmount" :disabled="isDisabled"></checkout-footer>

  </div>
  
</div>
</template>

<script>
import CheckoutNavBar from './CheckoutNavBar.vue'
import LoadingCover from '../shared/LoadingCover.vue'
import ErrorView from '../shared/ErrorView.vue'

import AddressCell from './AddressCell.vue'
import CheckoutItems from './CheckoutItems.vue'
import FapiaoCell from './FapiaoCell.vue'
import PointsSelect from './PointsSelect.vue'
import CheckoutTotals from './CheckoutTotals.vue'
import CheckoutFooter from './CheckoutFooter.vue'

import MessageBox from 'vue-msgbox'

export default {
  name: 'CheckoutView',

  components: {
    CheckoutNavBar,
    LoadingCover,
    ErrorView, 

    AddressCell,
    CheckoutItems,
    FapiaoCell,
    PointsSelect,
    CheckoutTotals,
    CheckoutFooter, 

    MessageBox
  },

  ready() {
    this.getCheckout()
  },

  route: {
    data ({ to }) {
      this.getCheckout()
      
      // callback to native
      didCheckout()
    }
  },

  data() {
    return {
      address: null, 
      checkoutItems: [],

      subTotalAmount: 0,
      shippingAmount: 0, 
      totalAmount: 0,

      isLoading: false,
      showError: false,
      isOrdering: false
    }
  },

  events: {
    didPickAddress(address) {
      console.log("[CheckoutView didPickAddress] address: " + JSON.stringify(address))
      this.$set("address", address)
      return true
    },

    didOrder() {
      console.log("[CheckoutView didOrder]")
      this.postOrder()
      return true
    }
  }, 

  computed: {
    /* check if order button should be disabled */
    isDisabled() {
      return !(this.address && (this.checkoutItems.length > 0) && !this.isOrdering)
    }, 

    /* check if user logged in */
    isUserLoggedIn() {
      if (typeof APP_SHOP_USER == 'undefined') {
        return false
      }
      
      return (APP_SHOP_USER && (APP_SHOP_USER != "TEMPLATE_APP_SHOP_USER"))
    },
  }, 

  methods: {

    /* 
      get checkout data from API, includes cart items in addition to shipping costs for current shipping address 
    */
    getCheckout() {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[getCheckout callback] response: " + JSON.stringify(response.body))
          let checkout = response.json()

          // cart items
          let cartItems = checkout.cart_items
          vm.$set('checkoutItems', cartItems)

          // shipping address
          let address = checkout.address
          vm.$set('address', address)

          // amounts
          let totalAmount = checkout.total_amount
          let subtotalAmount = checkout.subtotal_amount
          let shippingAmount = checkout.shipping_amount

          vm.$set('totalAmount', totalAmount)
          vm.$set('subtotalAmount', subtotalAmount)
          vm.$set('shippingAmount', shippingAmount)

          // remove loading cover
          vm.$set('isLoading', false)
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + JSON.stringify(response));

          // remove loading cover
          vm.$set('isLoading', false)

          // show error
          vm.$set('showError', true)
      }

      // request url 
      var url = API_URL + "/api/cart/checkout";

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET, 
          "X-APP-SHOP-USER": APP_SHOP_USER
      };

      // vue-resource
      this.$http.get(url, {headers: headers}).then(callback, errorCallback);
    },

    /*
      post order to API
      */
    postOrder() {
      // check user logged in
      if (!this.isUserLoggedIn) {
        return 
      }

      // disable order button
      this.$set("isOrdering", false)

      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[postOrder callback] response: " + JSON.stringify(response.body))
          let order = response.json()

          // native callback
          didOrder(order)

          MessageBox("成功", "订单成功，请支付", {
            type: "success"
          }).then(function(value, action) {
            // redirect to shopping_cart
            window.location.replace('#!/')
          })
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + JSON.stringify(response));

          // show error
          vm.$set('showError', true)
      }

      // request url 
      var url = API_URL + "/api/cart/order";

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET, 
          "X-APP-SHOP-USER": APP_SHOP_USER
      };

      // vue-resource
      this.$http.post(url, {}, {headers: headers}).then(callback, errorCallback);

    }
  }
}
</script>

<style>

/* checkout content below navbar */ 
#checkout {
  margin-top: 64px;
  padding-top: 20px;
  padding-bottom: 60px; 

  /*gray background simulate tableview*/
  background-color: #F6F6F6;
}

.cell-header {
  color: #666;
}

</style>


