<template>
  <div class="checkout-item">
    <div class="checkout-item-image">
      <img :src="item.square_cover_photo_url" />
    </div>

    <div class="right-content small">
      
      <div style="display: flex; justify-content: space-between;">
        <div>
          <h4 style="padding-top: 0;">{{ item.title }}</h4>
        </div>
        <div class="qty skew">X {{ qty }}</div>
      </div>

      <div class="skew">
        <span v-if="color">{{ color }}</span>
        <span v-if="size">{{ size }}</span>
      </div>

      <!-- <div style="position: absolute; bottom: 0; width: 100%;"> -->
        <div style="display: flex; justify-content: space-between;">
          <div class="price">{{ (item.price / 100) | currency "¥" 2  }}</div>
          <div class="skew">运费: {{ (shipping / 100) | currency "¥" 2  }}</div>
        </div>
      <!-- </div> -->

    </div>
  </div>  
</template>

<script>
export default {
  name: 'CheckoutItem',

  props: {
    item: Object,
    qty: Number,
    color: String,
    size: String,
    shipping: {
      type: Number,
      default: 0
    }
  }
}
</script>

<style>

  .checkout-item {
    display: flex;
    justify-content: left;
  }

  .checkout-item-image img {
    width: 100%;
    display: block;
    width: 80px;
  }

  .right-content {
    position: relative;
    width: 100%;
    padding-left: 8px;
  }

  .bottom-view {
    position: absolute;
    bottom: 5px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  .qty {
    width: 35px; margin-left: 7px;
  }

  .skew {
    color: #999;
  }

</style>


