<template>
<div id="addressList">

  <!-- navbar -->
  <checkout-nav-bar :path="'/checkout'" :title="'地址'"></checkout-nav-bar>
  
  <!-- error cover screen -->
  <error-view v-show="showError"></error-view>

  <!-- loading cover -->
  <loading-cover v-show="isLoading" transition="fade">
  </loading-cover>

  <div class="address-list-container">

    <div class="address-list">
      <address
        v-for="address in addressList" 
        :address="address">
      </address>
    </div>

    <section>
      <a href="#!/addresses/new"><div class="button success" style="width: 100%;">新增地址</div></a>
    </section>
  </div>

</div>
</template>

<script>
import CheckoutNavBar from '../checkout/CheckoutNavBar.vue'
import LoadingCover from '../shared/LoadingCover.vue'
import ErrorView from '../shared/ErrorView.vue'

import Address from './Address.vue'
import AddAddress from './AddAddress.vue'

export default {
  name: 'AddressList',

  components: {
    CheckoutNavBar,
    LoadingCover,
    ErrorView, 
    Address,
    AddAddress
  },

  ready() {
    this.getAddress()
  },

  data() {
    return {
      addressList: [], 
      pickedAddress: Object,
      isLoading: false,
      showError: false
    }
  },

  events: {
    didPickAddress(address) {
      this.$set("pickedAddress", address)
      this.pickAddress(address)
      return true
    }
  }, 

  methods: {

    /* 
      get address data from API
    */
    getAddress() {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[getAddress callback] response: " + JSON.stringify(response.body))
          let addressList = response.json()

          vm.$set('addressList', addressList)

          // remove loading cover
          vm.$set('isLoading', false)
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + JSON.stringify(response));

          // remove loading cover
          vm.$set('isLoading', false)

          // show error
          vm.$set('showError', true)
      }

      // request url 
      var url = API_URL + "/api/addresses";

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET, 
          "X-APP-SHOP-USER": APP_SHOP_USER
      };

      // vue-resource
      this.$http.get(url, {headers: headers}).then(callback, errorCallback);
    }, 

    /* 
      picked address
     */
     pickAddress(address) {

      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[pickAddress callback] response: " + JSON.stringify(response.body))

          // redirect
          window.location.href = "#!/checkout"

      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + JSON.stringify(response));

          // redirect
          window.location.href = "#!/checkout"
      }

      // request url 
      var url = API_URL + "/api/cart/checkout";

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET, 
          "X-APP-SHOP-USER": APP_SHOP_USER
      };

      // payload
      let params = {cart: {address: address}}

      // vue-resource
      this.$http.patch(url, params, {headers: headers}).then(callback, errorCallback);

     }, 
  }
}
</script>

<style>



/* checkout content below navbar */ 
#addressList {
  margin-top: 64px;
  padding-top: 20px;
  padding-bottom: 60px; 

  /*gray background simulate tableview*/
  background-color: #F6F6F6;

  height: 100%;
}


/* sticky bottom footer */
.checkout-footer {
  position: fixed;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  bottom: 0;
  z-index: 10;
}

.cell-header {
  color: #666;
}

</style>


