import Vue from 'vue'
import Resource from 'vue-resource'
import Router from 'vue-router'

import CartView from './components/CartView.vue'
import CartItemDetail from './components/cart_item/CartItemDetail.vue'
import CheckoutView from './components/checkout/CheckoutView.vue'
import AddressList from './components/address/AddressList.vue'
import AddAddress from './components/address/AddAddress.vue'

import App from './App.vue'

// stylesheets
require("picnic/src/picnic.scss");
require("./assets/stylesheets/application.scss")
require('vue-animate/dist/vue-animate.min.css')
require('vue-msgbox/lib/vue-msgbox.css') // msgbox css

// async requests
Vue.use(Resource)

// // router
Vue.use(Router)

// import mint-ui cell
import Mint from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(Mint)

var router = new Router()
router.map({
  '/': {
    component: CartView
  },
  '/cart': {
    component: CartView
  },
  '/items/:id': {
    component: CartItemDetail
  },
  '/checkout': {
    component: CheckoutView
  },
  '/addresses': {
    component: AddressList
  },
  '/addresses/new': {
    component: AddAddress
  }
})

router.beforeEach(function () {
  window.scrollTo(0, 0)
})

router.redirect({
  '*': '/cart'
})

router.start(App, "#app")



