<template>
  <div class="cart-item">
    <div class="cart-item-image">
      <a href="#/items/{{ cartItem.item.key }}">
        <img :src="cartItem.item.square_cover_photo_url" />
      </a>
    </div>

    <div class="cart-item-body small">
      <!-- <div class="small"> -->
        <h4 style="padding-top: 0;"><a href="#/items/{{ cartItem.item.key }}">{{ cartItem.item.title }}</a></h4>

        <div class="skew">
          <span v-if="cartItem.color">{{ cartItem.color }}</span>
          <span v-if="cartItem.size">{{ cartItem.size }}</span>
        </div>

        <div class="price">{{ (cartItem.item.price / 100) | currency "¥" 2  }}</div>
      <!-- </div> -->

      <div class="bottom-view" style="font-size: initial;">
        <div style="width: 120px;">
          <cart-item-qty-input :cart-item.sync="cartItem"></cart-item-qty-input>
        </div>
        <div class="button outline delete" @click="removeItem" :disabled="cartItem.qty > 0" >删除</div>
      </div>
    </div>
  </div>  
</template>

<script>
import CartItemQtyInput from './CartItemQtyInput.vue'

export default {
  name: 'CartItem',

  props: {
    cartItem: Object
  },

  components: {
    CartItemQtyInput
  },

  methods: {
    removeItem() {
      this.$dispatch('removeItem', this.cartItem)
    }
  },
}
</script>

<style>

  .cart-item {
    display: flex;
    justify-content: left;
  }

  .cart-item-image img {
    width: 100%;
    display: block;
    width: 80px;
  }

  .cart-item-body {
    position: relative;
    width: 100%;
    padding-left: 8px;
    flex-grow: 1;

    padding-bottom: 40px;
  }

  .bottom-view {
    position: absolute;
    bottom: 5px;
    width: 100%;
    padding-right: 14px; /* align with checkout button */
    display: flex;
    justify-content: space-between;
  }

  .skew {
    color: #999;
  }

</style>


