<template>
  <div class="cart-items-container">
    <section class="cart-items" v-if="cartItems && (cartItems.length > 0)">
      <cart-item 
        v-for="cartItem in cartItems" 
        :cart-item="cartItem">
      </cart-item>
    </section>

    <section v-else class="center island small mute">
      <p>你的购物车是空的~</p>
    </section>
  </div>

</template>


<script>
import CartItem from './CartItem.vue'

export default {
  name: 'CartItems',

  props: {
    cartItems: Array
  },

  components: {
    CartItem
  }
}
</script>

<style>

.cart-item + .cart-item {
  border-top: 1px solid #ddd;
  padding-top: 8px;
  margin-top: 16px;
}

</style>


