<template>
  <div class="address">

    <div v-if="address">
      <mt-cell 
        :title="address.name + '   电话: ' + address.tel" 
        :label="'地址: ' + address.full_street_address"
        is-link
        @click="didTap"
        class="edit"
      >
        <img slot="icon" v-bind:src="locationImgSrc" width="24" height="24" />
      </mt-cell>
    </div>

  </div>
</template>

<script>

export default {
  name: 'Address',

  props: {
    address: Object
  },

  data() {
    return {
      locationImgSrc: require("../../assets/images/location.png")
    }
  },

  methods: {
    didTap() {
      console.log("[Address didTap] address: " + JSON.stringify(this.address))
      this.$dispatch('didPickAddress', this.address)
    }
  }
}
</script>

<style>
.address {
  margin: 0 auto 20px;
}
</style>


