<template>
  <div id="checkout-totals">
    <section class="flex two">
      <div>总共</div>
      <div style="text-align: right;">{{ (subtotalAmount / 100) | currency "¥" 2 }}</div>

      <div>运费</div>
      <div style="text-align: right;">{{ (shippingAmount / 100) | currency "¥" 2 }}</div>

      <div class="orange">含运费</div>
      <div class="orange" style="text-align: right;">{{ (totalAmount / 100) | currency "¥" 2 }}</div>

    </section>
  </div>
</template>

<script>

export default {
  name: 'CheckoutTotals',

  props: {
    totalQty: Number,
    subtotalAmount: Number,
    shippingAmount: Number,
    totalAmount: Number
  },
}
</script>

<style>
#checkout-totals {
  margin: 5px auto 20px auto;
}

.orange {
  color: #FF7F00;
}

</style>


